<template>
  <div id="demo">
    <div class="my-editor" ref="editor"></div>
  </div>
</template>

<script>
import E from 'wangeditor'
import io from 'socket.io-client'
import { mapState } from 'vuex'

export default {
  name: 'editor',
  data () {
    return {
      e: null,
      socket: null,
      h: null
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    onchange (html) {
      console.log(html)
    },
  },
  mounted () {
    this.socket = io.connect('http://3000.grcb2295.l3xy9scq.e2ea52.grapps.cn/')
    this.socket.on('editing', (e) => {
      // 接收到新的内容
      console.log(`heared!`)
      if (this.h !== e.h) {
        this.e.txt.html(e.h)
      }
    })
    this.e = new E(this.$refs['editor'])
    this.e.customConfig.onchange = (html) => {
      // 发送修改的全部内容
      this.h = html
      const data = {
        mid: 123,
        token: this.user.token,
        data: {
          h: html
        },
      }
      console.log(data)
      this.socket.emit('editing', data)
    }
    this.e.customConfig.onchangeTimeout = 1000
    this.e.customConfig.menus = [
      'head', // 标题
      'bold', // 粗体
      'fontSize', // 字号
      'fontName', // 字体
      'italic', // 斜体
      'underline', // 下划线
      'strikeThrough', // 删除线
      'foreColor', // 文字颜色
      'backColor', // 背景颜色
      'link', // 插入链接
      'list', // 列表
      'justify', // 对齐方式
      'quote', // 引用
      'image', // 插入图片
      'table', // 表格
      'undo', // 撤销
    ]
    this.e.create()
    this.e.txt.html('<p>用 JS 设置的内容</p>')
    // 禁止编辑
    // this.e.$textElem.attr('contenteditable', false)
  }
}
</script>

<style lang="scss" scoped>
.my-editor {
  width: 100%;
  margin: 20px auto;
}
</style>
